import {
  Car,
  Construction,
  Electronic,
  Game,
  Gift,
  Home,
  Job,
  Man,
  Mountain,
} from "../../assets/icons/icons";
// import Gift from "./../../assets/images/Gifts.svg";
import "./homepage.scss";
import { NavLink, Outlet } from "react-router-dom";
import CenterMode from "../../components/swiper/swiper";
import { SliderCard } from "../../components/sliderCard/siliderCard";
import { ProductItemLimited } from "../../container/productItem/productItem";

function HomePage() {
  return (
    <>
      <div className="homepage__card-wrapper container">
        <SliderCard title="" bgColor="primary" icon={<Car />} />
        <SliderCard title="" bgColor="cyan" icon={<Home />} />
        <SliderCard
          title=""
          bgColor="orange"
          icon={<Electronic />}
        />
        <SliderCard title="" bgColor="yellow" icon={<Job />} />
        <SliderCard title=" " bgColor="lightOrange" icon={<Gift />} />
        <SliderCard title="  " bgColor="cyan" icon={<Mountain />} />
        <SliderCard
          title="  "
          bgColor="primary"
          icon={<Construction />}
        />
        <SliderCard
          title="  "
          bgColor="lightOrange"
          icon={<Game />}
        />
        <SliderCard title="  " bgColor="orange" icon={<Man />} />
      </div>
      <div className="homepage container">
        <div className="homepage__top">
          <NavLink
            style={{ borderBottom: "2px solid blue", paddingBottom: "10px" }}
            className="active"
            to="/"
            end
          >
            <span className="homepage__top-heading active">
               
            </span>
          </NavLink>
          <NavLink to="/best">
            <span className="homepage__top-heading"> </span>
          </NavLink>
          <Outlet />
        </div>
      </div>
      <CenterMode />
      <div className="homepage">
        <div className="homepage__top container">
          <a
            style={{ borderBottom: "2px solid blue", paddingBottom: "10px" }}
            className="homepage__top-heading active"
          >
            
          </a>
          <a className="homepage__top-heading"> </a>
          <a className="homepage__top-heading"> </a>
          <ProductItemLimited />
        </div>
      </div>
    </>
  );
}

export default HomePage;
