import { NavLink, Outlet } from "react-router-dom";
import "./profile.scss";

const Profile = () => {
  return (
    <>
      <div className="profile">
        <div className="profile__wrapper container">
          <div className="profile__wrapper-header">
            <div className="profile__header-top">
              <div className="profile__header-left">
                <h3 className="profile__left-title">
                    
                </h3>
              </div>
              <div className="profile__header-right">
                <h3 className="profile__right-title"> : 2 3000 </h3>
                <button className="profile__header-right--btn">
                  
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        style={{ marginTop: "20px" }}
        className="profile__link-wrapper container"
      >
        <NavLink to="/profile" className="profile__link-navlink" end>
          
        </NavLink>
        <NavLink className="profile__link-navlink" to="/profile/message">
          
        </NavLink>
        <NavLink className="profile__link-navlink" to="/profile/account">
            
        </NavLink>
        <NavLink className="profile__link-navlink" to="/profile/settings">
          
        </NavLink>
        <NavLink className="profile__link-navlink" to="/profile/business">
           
        </NavLink>
      </div>
      <div className="profile__main">
        <div className="profile__main-info">
          <div className="profile__main-menu container">
            <h3 className="profile__menu-title"></h3>
            <h3 className="profile__menu-title"></h3>
            <h3 className="profile__menu-title"></h3>
            <h3 className="profile__menu-title"></h3>
            <h3 className="profile__menu-title"></h3>
          </div>
        </div>
      </div>
      <Outlet />
    </>
  );
};

export default Profile;
