import { useEffect, useState } from "react";
import { NavLink, useParams } from "react-router-dom";
import { ProductItemLimited } from "../../container/productItem/productItem";
import "./singleProduct.scss";

function SingleProduct() {
  const params = useParams();
  const [products, setProducts] = useState({
    isFetched: false,
    data: [],
    error: null,
  });

  useEffect(() => {
    fetch(`https://fakestoreapi.com/products/${params.id}`)
      .then((res) => res.json())
      .then((data) => {
        setProducts({
          isFetched: true,
          data: data,
          error: false,
        });
        // console.log(data);
      })
      .catch((err) => {
        setProducts({
          isFetched: false,
          data: [],
          error: err,
        });
      });
  }, []);
  return (
    <>
      <div className="singlePage ">
        <div className="container">
          <div className="singlePage__top">
            <h4 className="singlePage__top-navigate"></h4>
            <span>/</span>
            <h4 className="singlePage__top-navigate"> </h4>
            <span>/</span>
            <h4 className="singlePage__top-navigate"> </h4>
            <span>/</span>
            <h4 className="singlePage__top-navigate"> </h4>
            <span>/</span>
            <h4 className="singlePage__top-navigate"> </h4>
          </div>

          <div className="singlePage__center-wrapper">
            <h2 className="singlePage__center-title">{products.data.title}</h2>

            <div className="singlePage__product">
              <div className="singlePage__product-left">
                <img
                  style={{ marginTop: "80px" }}
                  src={products.data.image}
                  width="500"
                  height="auto"
                  alt="image"
                />
              </div>

              <div className="singlePage__product-right">
                <div className="singlePage__right-top">
                  <h4 className="singlePage__top-heading">: 4418970</h4>

                  <h4 className="singlePage__top-heading">
                    : 28.04.17 : 28.04.17
                  </h4>
                </div>

                <h3 className="singlePage__top-price">
                  {products.data.price} ..
                </h3>

                <a href="#" className="singlePage__top-link">
                  ,  ,  
                </a>

                <div className="singlePage__customer-info">
                  <h3 className="singlePage__info-phone">998 91 166 ****</h3>
                  <button className="singlePage__info-btn">
                     
                  </button>
                </div>

                <div className="singlePage__customer-info">
                  <h3 className="singlePage__info-phone">998 91 166 ****</h3>
                  <button className="singlePage__info-btn">
                     
                  </button>
                </div>

                <div className="singlePage__auth-info">
                  <h4 className="singlePage__auth-heading">
                     :
                  </h4>
                  <h5 className="singlePage__auth-title"> </h5>
                </div>

                <div className="singlePage__contact-info">
                  <button className="singlePage__contact-btn">
                     
                  </button>
                  <button className="singlePage__contact-btn activeBTN">
                      
                  </button>
                </div>

                <div className="singlePage__table">
                  <div className="singlePage__table-left">
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">
                         :
                      </h4>
                      <h4 className="singlePage__table-text">6</h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">
                         :
                      </h4>
                      <h4 className="singlePage__table-text">6</h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title"></h4>
                      <h4 className="singlePage__table-text special">
                        
                      </h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title"></h4>
                      <h4 className="singlePage__table-text special">
                        2   
                      </h4>
                    </div>
                  </div>
                  <div className="table-center">
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">
                         :
                      </h4>
                      <h4 className="singlePage__table-text">40 2</h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">
                         :
                      </h4>
                      <h4 className="singlePage__table-text"> </h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">
                         /
                      </h4>
                      <h4 className="singlePage__table-text special">
                          2017
                      </h4>
                    </div>
                    <div className="singlePage__table-item small">
                      <h4 className="singlePage__table-title"></h4>
                      <h4 className="singlePage__table-text special"></h4>
                    </div>
                  </div>
                  <div className="table-right">
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">:</h4>
                      <h4 className="singlePage__table-text">32</h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">:</h4>
                      <h4 className="singlePage__table-text">32</h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title"></h4>
                      <h4 className="singlePage__table-text">
                         
                      </h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">
                         
                      </h4>
                      <h4 className="singlePage__table-text">32</h4>
                    </div>
                  </div>
                </div>

                <div className="singlePage__table-bottom">
                  <div className="singlePage__table-item">
                    <h4 className="singlePage__table-title"> </h4>
                    <h4 className="singlePage__table-text special">
                      , , , ,  ,
                      , ,  ,  ,
                      , 
                    </h4>
                  </div>
                </div>

                <div className="singlePage__about">
                  <h4 className="singlePage__about-text">
                    {products.data.description}
                  </h4>
                </div>

                <div className="add-info">
                  <h4 className="add-info__left">
                    :
                    <span>10958</span>
                  </h4>

                  <a href="#" className="add-info__right">
                    
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div
            style={{ marginBottom: "21px" }}
            className="homepage__top container"
          >
            <NavLink
              style={{ borderBottom: "2px solid blue" }}
              className="homepage__top-heading active"
              to="/"
              end
            >
              
            </NavLink>
            <a className="homepage__top-heading"> </a>
            <a className="homepage__top-heading"> </a>
          </div>
          <ProductItemLimited />
        </div>
      </div>
    </>
  );
}

export default SingleProduct;
